.lists_container {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 10;
  width: 100px;
  height: 20px;

  border: 1px solid lightgray;
  border-radius: 5px;
}

.lists_select_element {
  width: 100%;
  display: flex;
  justify-content: center;

  &:hover {
      cursor: pointer;
  }
}